import { Link } from "react-router-dom";
import logo from '../images/logo.png';
import routine from '../images/routine.PNG';
import track from '../images/track.PNG';

export function HomePage() {
    
    return (
      <div className="mainPage">
        <div className="HeaderSection">
          <div className="headerLeft">
            <img className="logo" src={logo}></img>
            <div>
              <h1>WORKOUT</h1>
              <h1>BUDDY</h1>
            </div>
          </div>
          <div className="headerRight">
            <div className="headerRightLinks">
              <div><Link className="return" to='/register'>REGISTER</Link></div>
              <div><Link className="return" to='/login'>LOG IN</Link></div>
            </div>
          </div>
        </div>
        <div className="sectionArea1">
          <div className="InfoSection">
              <h2>Create your own routine</h2>
              <img className="promoImage" src={routine}></img>
          </div>
          <div className="InfoSection">
              <h2>Track your progress</h2>
              <img className="promoImage" src={track}></img>
          </div>
        
        <div className="LoginSection">
          <div className="sectionArea">
            <div>
              <h2>Start now!</h2>
              <div><Link className="return" to='/register'>REGISTER</Link></div>
            </div>
            <div>
              <h2>Already have an account?</h2>
              <div><Link className="return" to='/login'>LOG IN</Link></div>
            </div>
          </div>
        </div>
        </div>
        <div className="ContactSection">
          <h3>Website created by Bartosz Wawrejko</h3>
          <a href='https://github.com/Bart-Waw' target="_blank">My GitHub page</a>
          <a href='https://www.freepik.com/photos/background' target="_blank">Background photo created by topntp26 - www.freepik.com</a>
        </div>
      </div>
    );
  }