import { Link } from "react-router-dom";
import logo from '../images/logo.png';

export function RegisteredPage() {

    return (
      <div className="SidePage">
        <div className="HeaderSection">
        <div className="headerLeft">
        <img className="logo" src={logo}></img>
            <div>
              <h1>WORKOUT</h1>
              <h1>BUDDY</h1>
            </div>
          </div>
        </div>
        <div className="MainSection">
          <div className="registeredSection">
            <h3>Registration successfull!</h3>
            <div>
              <h4>Go to your profile to set your goal</h4>
              <h4>and start tracking your progress</h4>
            </div>
            <Link className='toProfile' to='/userpage/details'>To your profile</Link>
          </div>
        </div>
      </div>
    );
  }