import axios from 'axios';
import { useEffect, useState } from 'react';
const Cookie = require('js-cookie');

export function UserStats() {
  const [exercisesList, setExercisesList] = useState(null);
  const [week, setWeek] = useState(1);

    const toggleView = async (weekDay) => {
        try {
          const currentRoutine = await axios.get(`/${Cookie.get('userID')}/${weekDay ? weekDay : week}/completedExcerciseList`); 
          if (currentRoutine.data.length !== 0) { 
            const currentRoutineFinal = currentRoutine.data.map(item => {
              return (<tr key={item.id} className={item.completed ? "excerciseCompleted" : "excerciseIncomplete"}>
                <td>{item.week_day}</td>
                <td>{item.excercise}</td>
                <td>{item.reps}</td>
                <td>{item.sets}</td>
                <td>{item.weight}</td>
                <td>{item.completed ? 'YES' : 'NO'}</td>
              </tr>)
            })
            setExercisesList(currentRoutineFinal);
            }
          else {
            setExercisesList(null);
          }
        }
        catch(error) {console.log(error)}
      }

    const handlePrevious = () => {
      if (week > 1) {
        setWeek(week - 1);
        toggleView(week - 1);
      }
    }
  
    const handleNext = () => {
      if (week < Cookie.get('goal')) {
        setWeek(week + 1);
        toggleView(week + 1);
      }
    }

    useEffect(() => {
      toggleView()
    }, [week])


    return (
      <div className="userPageStats">
        <div className="weekNavigation">
          <div>Week: {week}</div>
          <div>
            <button className="weekNav" onClick={handlePrevious}>Previous</button>
            <button className="weekNav" onClick={handleNext}>Next</button>
          </div>
        </div>
        <div className="exerciseList">
          <table>
            <thead>
              <tr>
                <th>Day</th>
                <th>Exercise</th>
                <th>Repetitions</th>
                <th>Sets</th>
                <th>Weight</th>
                <th>Completed</th>
              </tr>
            </thead>
            <tbody>
            {exercisesList !== null && exercisesList}
            </tbody>
            
          </table>
        </div>
      </div>
    );
  }