import { useState } from "react";
import { BrowserRouter, Link, Route } from "react-router-dom";
import { UserDetails } from "./userDetails";
import { UserMain } from "./userMain";
import { UserStats } from "./userStats";
import logo from '../images/logo.png';
const Cookie = require('js-cookie');

export function UserPage() {
  const [user, setUser] = useState(Cookie.get('userName'));

    const handleLogout = () => {
      Cookie.set('userName', null);
      Cookie.set('userEmail', null);
      Cookie.set('userID', null);
      Cookie.set('userIsAdmin', null);
      Cookie.set('goal', null);
      setUser("");
    }


    const handleClick = (event) => {
      let previous = document.getElementsByClassName("mainNavActive");
      for (let i = 0; i < previous.length; i++) {
        previous[i].className = "nav";
      }
      let clicked = event.target;
      clicked.className = "mainNavActive";
    }

    return (
      <div className="userPage">
        <div className="HeaderSection">
          <div className="headerLeft">
            <img className="logoMini" src={logo}></img><h3>WORKOUT BUDDY</h3>
          </div>
          <div className="userHeaderRight">
          <h3>Hello {user}</h3>
          <Link  to='/logout' onClick={handleLogout} id="logoutButton" className='mainNav'>Log Out</Link>
          </div>
        </div>
        <BrowserRouter>
            <div className="Navigation">
                <div><Link className='nav' to='/userpage/main' onClick={handleClick}>WORKOUT MANAGER</Link></div>
                <div><Link className='nav' to='/userpage/stats' onClick={handleClick}>TRACKER</Link></div>
                <div><Link className='nav' to='/userpage/details' onClick={handleClick}>MY DETAILS</Link></div>
            </div>
            <div className="MainSection">
                <Route path="/userpage/main" component={UserMain}/>
                <Route path="/userpage/stats" component={UserStats}/>
                <Route path="/userpage/details" component={UserDetails}/>
            </div>
        </BrowserRouter>
      </div>
    );
  }