import axios from "axios";
import { useEffect, useState } from "react";
const Cookie = require('js-cookie');

export function UserMain() {
  const [day, setDay] = useState('Monday');
  const [week, setWeek] = useState(1);
  const [routine, setRoutine] = useState('');

  const handleDelete = async (event) => {
    await axios.post(`/deleteExcercise`, {"id": event.target.value});
    toggleView(day, week);
  };

  const handleComplete = async (event) => {
    event.preventDefault();
      const completedExcercise = {
        excerciseid: event.target.value
      }
      try {
        await axios.post('/completeExcercise', completedExcercise);
        toggleView(day, week);
      }
      catch(error) {console.log(error)}
  }

  const toggleView = async (viewDay, viewWeek) => {
    let previous = document.getElementsByClassName("navActive");
    for (let i = 0; i < previous.length; i++) {
      previous[i].className = "nav";
    }
    let clicked = document.getElementById(viewDay);
    clicked.className = "navActive";
    try {
      const currentRoutine = await axios.get(`/${Cookie.get('userID')}/${viewDay}/${viewWeek}/excerciseList`); 
      if (currentRoutine.data.length !== 0) {
        const currentRoutineFinal = currentRoutine.data.map(item => {
            return (<tr key={item.id}>
                      <td><button onClick={handleDelete} value={item.id}>X</button></td>
                      <td>{item.excercise}</td>
                      <td>{item.reps}</td>
                      <td>{item.sets}</td>
                      <td>{item.weight}</td>
                      <td>
                        {item.completed ? 
                        "completed" :
                        <button name="idRepsSetsNameWeight" value={item.id} onClick={handleComplete}>Complete</button>}
                      </td>
                    </tr>)
        })
        setRoutine(currentRoutineFinal);
      }
      else {
        setRoutine("");
      }
      setDay(viewDay);
    }
    catch(error) {console.log(error)}
  }

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!event.target.excercise.value || !event.target.sets.value || !event.target.reps.value || !event.target.weights.value) {
      window.alert("All fields must be completed");
    }
    else {
      const newExercise = {
        userid: Cookie.get('userID'),
        week_day: day,
        excercise: event.target.excercise.value,
        sets: event.target.sets.value,
        reps: event.target.reps.value,
        weights: event.target.weights.value,
        week_number: week
      }
      try {
        await axios.post('/addExcercise', newExercise);
        toggleView(day, week);
      }
      catch(error) {console.log(error)}
    }

  }

  const handlePrevious = () => {
    if (week > 1) {
      setWeek(week - 1);
    }
    toggleView(day, week);
  }

  const handleNext = () => {
    if (week < Cookie.get('goal')) {
      setWeek(week + 1);
    }
    toggleView(day, week);
  }

  useEffect(() => {
    toggleView(day, week)
  }, [day, week])

    return (
      <div className="userPageMain">
              <div className="dayNavigation">
                  <button onClick={() => toggleView("Monday", week)} id="Monday" className='nav'>Monday</button>
                  <button onClick={() => toggleView("Tuesday", week)} id="Tuesday" className='nav'>Tuesday</button>
                  <button onClick={() => toggleView("Wednesday", week)} id="Wednesday" className='nav'>Wednesday</button>
                  <button onClick={() => toggleView("Thursday", week)} id="Thursday" className='nav'>Thursday</button>
                  <button onClick={() => toggleView("Friday", week)} id="Friday" className='nav'>Friday</button>
                  <button onClick={() => toggleView("Saturday", week)} id="Saturday" className='nav'>Saturday</button>
                  <button onClick={() => toggleView("Sunday", week)} id="Sunday" className='nav'>Sunday</button>
              </div>

              <div className="daySection">
                  <div className="weekDay">
                    <div className="routine">
                      <div className="weekNavigation">
                        <div className="weekNavigationSub">
                          <div>Week: {week}</div>
                          <div>
                            <button className="weekNav" onClick={handlePrevious}>Previous</button>
                            <button className="weekNav" onClick={handleNext}>Next</button>
                          </div>
                        </div>
                      </div>
                      {routine !== "" ? (
                      <table>
                        <thead>
                            <tr>
                                <th>remove</th>
                                <th>exercise name</th>
                                <th>repetitions</th>
                                <th>sets</th>
                                <th>weights</th>
                                <th>complete exercise</th>
                            </tr>
                        </thead>
                        <tbody>
                          {routine}
                        </tbody>
                      </table>) 
                    : 
                        <div id="noWork">
                          <h3>No workout today!</h3>
                        </div>
                      }
                      </div>
                    <form onSubmit={handleSubmit} className="excerciseForm">
                      <h3>Add new exercise</h3>
                      <div>
                          <input type='text' name='excercise'></input>
                          <label htmlFor='excercise'>exercise name</label>
                      </div>
                      <div>
                          <input type='number' min="1" name='reps'></input>
                          <label htmlFor='reps'>number of repetitions</label>
                      </div>
                      <div>
                          <input type='number' min="1" name='sets'></input>
                          <label htmlFor='sets'>number of sets</label>
                      </div>
                      <div>
                          <input type='text' min="1" name='weights'></input>
                          <label htmlFor='weights'>weights (lbs/kgs)</label>
                      </div>
                      <button type='submit'>ADD</button>
                    </form>
                  </div>
              </div>
      </div>
    );
  }