import { Link } from "react-router-dom";
import axios from "axios";
import logo from '../images/logo.png';
const Cookie = require('js-cookie');

export function LoginPage() {

  const handleSubmit = async (event) => {
    event.preventDefault();
    const loggingUser = {
        email: event.target.email.value,
        password: event.target.password.value
    }
    try {
        const response = await axios.post('/login', loggingUser);
        if (response.data) {
          Cookie.set('userName', response.data.name);
          Cookie.set('userEmail', response.data.email);
          Cookie.set('userID', response.data.id);
          Cookie.set('userIsAdmin', response.data.isadmin);
          Cookie.set('goal', response.data.goal);
          window.location.href = '/userpage/main';
        }
    }
    catch(error) {window.alert("incorrect email or password");}
    };

    return (
      <div className="SidePage">
        <div className="HeaderSection">
        <div className="headerLeft">
        <img className="logo" src={logo}></img>
            <div>
              <h1>WORKOUT</h1>
              <h1>BUDDY</h1>
            </div>
          </div>
        </div>
        <div className="MainSection">
            <div className="formContainer">
                <form onSubmit={handleSubmit}>
                  <div>
                    <div className="formInput">
                        <label htmlFor='email'>email</label>
                        <input type='text' name='email'></input>
                    </div>
                    <div className="formInput">
                        <label htmlFor='password'>password</label>
                        <input type='password' name='password'></input>
                    </div>
                    <button className='registerLoginButtons' type='submit'>LOG IN</button>
                    </div>
                    <Link className='registerLoginButtons' to='/'>RETURN</Link>
                </form>
            </div>
        </div>
      </div>
    );
  }