import axios from "axios";
import { useState } from "react";
const Cookie = require('js-cookie');

export function UserDetails() {
  const [view, setView] = useState('');

  const toggleView = async () => {
    const user = await axios.get(`/users/${Cookie.get('userID')}`);
    setView(<div className="userDetails">
      Name:  <h3>{user.data[0].name}</h3>
      Email: <h3>{user.data[0].email}</h3>
      My goal: <h3>{user.data[0].goal} {user.data[0].goal === 1 ? "week" : "weeks"}</h3>
    </div>)
  }

  const handleSubmit = async (event) => {
    if (event.target.goal.value) {
      await axios.post(`/users/${Cookie.get('userID')}/changeGoal`, {goal: event.target.goal.value});
      Cookie.set('goal', event.target.goal.value);
      toggleView()
    }
  }

  useState(() => {
    toggleView()
  }, [view])


    return (
      <div className="userDetailsMain">
        {view}
        <div className="userDetails">
          <h4>Change your goal</h4>
          <form id="goalForm" onSubmit={handleSubmit}>
            <div>New goal: <input type="number" name="goal" min="1"></input>weeks<button type="submit">confirm</button></div>
          </form>
        </div>
      </div>
    );
  }